import {Injectable} from '@angular/core';
const Injectable: NeverTypeCheckMe = Injectable;  /* local alias for Closure JSDoc */

const /** @type {!Array<string>} */ WEEKDAYS_SHORT = ['Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa', 'Su'];
const /** @type {!Array<string>} */ MONTHS_SHORT = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
const /** @type {!Array<string>} */ MONTHS_FULL = [
  'January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November',
  'December'
];
/**
 * Type of the service supplying month and weekday names to to NgbDatepicker component.
 * See the i18n demo for how to extend this class and define a custom provider for i18n.
 * @abstract
 */
@Injectable()
export abstract class NgbDatepickerI18n {
/**
 * Returns the short weekday name to display in the heading of the month view.
 * With default calendar we use ISO 8601: 'weekday' is 1=Mon ... 7=Sun
 * @abstract
 * @param {number} weekday
 * @return {string}
 */
getWeekdayShortName(weekday: number) {}
/**
 * Returns the short month name to display in the date picker navigation.
 * With default calendar we use ISO 8601: 'month' is 1=Jan ... 12=Dec
 * @abstract
 * @param {number} month
 * @return {string}
 */
getMonthShortName(month: number) {}
/**
 * Returns the full month name to display in the date picker navigation.
 * With default calendar we use ISO 8601: 'month' is 1=January ... 12=December
 * @abstract
 * @param {number} month
 * @return {string}
 */
getMonthFullName(month: number) {}
}
@Injectable()
export class NgbDatepickerI18nDefault extends NgbDatepickerI18n {
/**
 * @param {number} weekday
 * @return {string}
 */
getWeekdayShortName(weekday: number): string { return WEEKDAYS_SHORT[weekday - 1]; }
/**
 * @param {number} month
 * @return {string}
 */
getMonthShortName(month: number): string { return MONTHS_SHORT[month - 1]; }
/**
 * @param {number} month
 * @return {string}
 */
getMonthFullName(month: number): string { return MONTHS_FULL[month - 1]; }
}
